@import url(https://fonts.googleapis.com/css2?family=Itim&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400&display=swap);
* {
	margin: 0;
	padding: 0;
	scroll-behavior: smooth;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	font-size: 62.5%;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

body {
	/* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif; */
	/* font-family: 'Itim', cursive; */
	font-family: 'Montserrat', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #ececec;
	display: flex;
	justify-content: center;
	align-items: center;
}

code {
	font-family: 'Montserrat', sans-serif;
}

.container {
	min-width: 100rem;
	min-height: 95vh;
	margin: 1.5rem auto;
	border-radius: 20px;
	background: #ececec;
	box-shadow: 9px 9px 16px rgba(163, 177, 198, 0.6),
		-9px -9px 16px rgba(255, 255, 255, 0.5),
		inset 3px 3px 7px rgba(136, 165, 191, 0.48), inset -3px -3px 7px #ffffff;
	padding: 5px;
	display: flex;
	flex-direction: column;
	align-items: stretch;
}

.content {
	display: flex;
	align-items: stretch;
	flex: 1 1;
}

.header {
	height: 7rem;
	padding: 1rem;
	border-radius: 20px;
	background: #ececec;
	box-shadow: 5px 5px 10px #c9c9c9;
	transition: box-shadow 399ms ease-in-out;
	display: flex;
	justify-content: space-between;
}

.header__title {
	font-weight: 100;
	font-size: 45px;
}

.header__signup--btn {
	cursor: not-allowed;
	width: 40px;
	height: 40px;
	background: #ececec;
	border-radius: 12px;
	box-shadow: inset 0 0 15px rgba(55, 84, 170, 0),
		inset 0 0 20px rgba(255, 255, 255, 0), 7px 7px 15px rgba(55, 84, 170, 0.15),
		-7px -7px 20px white, inset 0px 0px 4px rgba(255, 255, 255, 0.2);
	transition: box-shadow 399ms ease-in-out;
	display: flex;
	justify-content: center;
	align-items: center;
}

.header-icon {
	stroke-width: 1px;
}

