	top: -0.2rem;
	box-shadow: inset 7px 7px 15px rgba(55, 84, 170, 0.15),
		inset -7px -7px 20px white, 0px 0px 4px rgba(255, 255, 255, 0.2);
} */

.nav {
	border-radius: 25px;
	background: #ececec;
	border-radius: 20px;
	box-shadow: 5px 5px 10px #c9c9c9, 0px -2px 10px #ffffff;
	flex: 0 0 25%;
	padding: 1rem;
	margin-top: 1rem;
	transition: 0.3s;
	font-size: 1.6rem;
	text-align: center;
	display: flex;
	justify-content: space-between;
	list-style: none;
	flex-direction: column;
}

.nav__toggle {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}

.nav__toggle-container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 10px 5px 10px 5px;
	margin: 20px 0 0;
}

.nav__title {
	font-size: 25px;
	margin: 15px 0 20px;
	color: #444444;
	font-weight: 400;
	text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1),
		-5px -5px 10px rgba(255, 255, 255, 1);
}

.nav__title-icon {
	height: 30px;
	width: 30px;
	margin-right: 10px;
	stroke-width: 0.5px;
	position: relative;
	top: 7px;
}

.nav__underline {
	margin-top: 30px;
	width: 100%;
	height: 5px;
	border-radius: 90px;
	background: #ececec;
	box-shadow: inset 1px 1px 3px #c9c9c9, inset -1px -1px 3px #ffffff;
}

.label-title {
	display: inline;
}

.nav__label {
	border-radius: 13px;
	font-weight: 300;
	background: #ececec;
	height: 47px;
	padding: 0 10px 20px 5px;
	transition: box-shadow 200ms ease-out;
}

.nav__label.on {
	box-shadow: 10px 10px 20px #c9c9c9, -10px -10px 20px #ffffff;
}

.nav-icon {
	height: 30px;
	stroke-width: 0.5px;
	margin: 0 15px 0 0px;
	position: relative;
	top: 8px;
}

.nav-toggle {
	outline: none;
	border: none;
	width: 51px;
	height: 30px;
	padding: 4px;
	border-radius: 30px;
	display: flex;
	align-items: center;
	cursor: pointer;
	transition: all 0.3s ease-out;
	background-color: #ececec;
	box-shadow: 9px 9px 16px rgba(163, 177, 198, 0.6),
		-9px -9px 16px rgba(255, 255, 255, 0.5),
		inset 3px 3px 7px rgba(136, 165, 191, 0.48), inset -3px -3px 7px #ffffff;
}

.nav-toggle.on {
	background-color: #ececec;
	box-shadow: 9px 9px 16px rgba(163, 177, 198, 0.6),
		-9px -9px 16px rgba(255, 255, 255, 0.5),
		inset 3px 3px 7px rgba(136, 165, 191, 0.48), inset -3px -3px 7px #ffffff;
}

.nav-toggle div {
	width: 24px;
	height: 24px;
	border-radius: 200px;
	transition: all 0.3s ease-out;
	transform: translateX(0px);
	background-color: #ececec;
	box-shadow: -8px -4px 8px 0px #ffffff88, 8px 4px 12px 0px #d1d9e6;
}

.nav-toggle.on div {
	background-color: #5cc74e;
	transform: translateX(18px);
	box-shadow: -8px -4px 8px 0px #ffffff8e, 8px 4px 12px 0px #d1d9e6;
}

.list {
	padding-top: 1rem;
	margin: 0.5rem;
	flex: 1 1;
	position: relative;
}

.list__container {
	display: flex;
	margin-top: 20px;
	margin-left: 15px;
	flex-direction: column;
	height: 480px;
	overflow-y: auto;
	overflow-x: hidden;
}

.btn-add {
	width: 55px;
	height: 55px;
	margin: 20px 8px 8px;
	border: 1px solid #69d1dd;
	outline: none;
	background: linear-gradient(#5ccfdb, #67b3f5);
	border-radius: 50%;
	box-shadow: inset 0 0 15px rgba(55, 84, 170, 0),
		inset 0 0 20px rgba(255, 255, 255, 0), 7px 7px 15px rgba(55, 84, 170, 0.15),
		-7px -7px 20px white, inset 0px 0px 4px rgba(255, 255, 255, 0.2);
	transition: box-shadow 399ms ease-in-out;
	position: absolute;
	bottom: 0;
	right: 0;
}

.btn-add:hover {
	box-shadow: inset 7px 7px 15px rgba(47, 79, 175, 0.15),
		inset -7px -7px 20px #4f9fe6, 0px 0px 4px rgba(255, 255, 255, 0.2);
}

.btn-add:active {
	border: none;
	outline: none;
}

.add-icon {
	height: 30px;
}

.plus-icon {
	color: #292c31;
	height: 50px;
	width: 50px;
	stroke-width: 0.5px;
}

.list-item {
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	width: 700px;
	margin: 1rem 1rem 1.5rem 1rem;
	padding: 0.6rem 1rem 1rem 1rem;
	border-radius: 20px;
	background: #ececec;
	box-shadow: 5px 5px 10px #c9c9c9, -5px -5px 10px #ffffff;
}

.list-item__container {
	display: flex;
	flex-direction: column;
	font-family: 'Itim', cursive;
}

.list-item__header {
	display: flex;
}

.list-item__title {
	text-transform: capitalize;
	font-size: 30px;
	color: #555555;
	font-weight: 200;
	margin: 0 15px 0 0;
	text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1),
		-5px -5px 10px rgba(255, 255, 255, 1);
}

.list-item__type {
	font-size: 15px;
	color: #aaa;
	text-transform: capitalize;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: flex-end;
	top: -3px;
}

.list-item__icon-container {
	margin-left: auto;
	margin-right: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	top: -3px;
}

.list-item__icon {
	margin-top: 3px;
	cursor: pointer;
	stroke-width: 1.2px;
}

.list-item__underline {
	width: 100%;
	height: 2px;
	border-radius: 90px;
	background: #ececec;
	box-shadow: inset 1px 1px 3px #c9c9c9, inset -1px -1px 3px #ffffff;
}

.list-item__description {
	font-size: 16px;
	display: flex;
	width: 100%;
	padding: 0 10px 0 10px;
	word-break: break-all;
	overflow-y: auto;
}

.list-item__footer {
	display: flex;
	justify-content: space-between;
}

.list-item__date {
	font-size: 14px;
	width: 100px;
	height: 30px;
	display: flex;
	justify-content: center;
	color: #555555;
	border-radius: 10px;
	align-items: center;
	margin-top: 13px;
	box-shadow: 9px 9px 16px rgba(163, 177, 198, 0.6),
		-9px -9px 16px rgba(255, 255, 255, 0.5),
		inset 3px 3px 7px rgba(136, 165, 191, 0.48), inset -3px -3px 7px #ffffff;
}

.list-item__buttons {
	display: flex;
	margin-right: 45px;
	margin-top: 8px;
}

.list-item__btn {
	width: 40px;
	height: 40px;
	margin: 2px 10px 0px 10px;
	background: #ececec;
	border-radius: 12px;
	box-shadow: inset 0 0 15px rgba(55, 84, 170, 0),
		inset 0 0 20px rgba(255, 255, 255, 0), 7px 7px 15px rgba(55, 84, 170, 0.15),
		-7px -7px 20px white, inset 0px 0px 4px rgba(255, 255, 255, 0.2);
	transition: box-shadow 399ms ease-in-out;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
}
.list-item__btn .icon {
	height: 20px;
	width: 20px;
	stroke-width: 2px;
}
.list-item__btn:hover {
	box-shadow: inset 7px 7px 15px rgba(55, 84, 170, 0.15),
		inset -7px -7px 20px white, 0px 0px 4px rgba(255, 255, 255, 0.2);
}

.icon-complete {
	color: #54ce3c;
}

.icon-undo {
	color: #9dabc0;
}

.icon-delete {
	color: #ff4e4e;
}

.disabled {
	cursor: not-allowed;
	-webkit-filter: blur(0.7px);
	        filter: blur(0.7px);
	opacity: 0.7;
}

.disabled:hover {
	box-shadow: inset 0 0 15px rgba(55, 84, 170, 0),
		inset 0 0 20px rgba(255, 255, 255, 0), 7px 7px 15px rgba(55, 84, 170, 0.15),
		-7px -7px 20px white, inset 0px 0px 4px rgba(255, 255, 255, 0.2);
}

.modal {
	z-index: 100;
	position: fixed;
	background: #e9eef7;
	top: 50%;
	left: 50%;
	width: 80rem;
	height: 50rem;
	border-radius: 40px;
	padding: 3rem;
	font-family: 'Itim', cursive;
}

.modal__btn-container {
	display: flex;
	justify-content: center;
}

.modal__btn {
	/* font-family: 'Itim', cursive;*/
	font-family: 'Montserrat', sans-serif;
	height: 50px;
	font-size: 18px;
	font-weight: 400;
	padding: 15px;
	margin: 15px;
	outline: none;
	border: none;
	border-radius: 15px;
	border: 1px solid #69d1dd;
	background: linear-gradient(#5ccfdb, #67b3f5);
	box-shadow: inset 0 0 15px rgba(55, 84, 170, 0),
		inset 0 0 20px rgba(255, 255, 255, 0), 7px 7px 15px rgba(55, 84, 170, 0.15),
		-7px -7px 20px white, inset 0px 0px 4px rgba(255, 255, 255, 0.2);
	transition: box-shadow 299ms ease-in-out;
}

.modal__btn:hover {
	box-shadow: inset 7px 7px 15px rgba(47, 79, 175, 0.15),
		inset -7px -7px 20px #4f9fe6, 0px 0px 4px rgba(255, 255, 255, 0.2);
}

.add-icon {
	stroke-width: 1.2px;
	size: 16px;
	position: relative;
	float: left;
	left: -8px;
	top: -3px;
}

.clear-icon {
	stroke-width: 1px;
	size: 16px;
	position: relative;
	float: left;
	left: -8px;
	top: -2px;
}

.x-icon {
	stroke-width: 0.9px;
	height: 30px;
	width: 30px;
}

.modal__btn-close {
	width: 50px;
	height: 50px;
	margin: 15px;
	border: 1px solid #69d1dd;
	outline: none;
	background: linear-gradient(#5ccfdb, #67b3f5);
	border-radius: 50%;
	box-shadow: inset 0 0 15px rgba(55, 84, 170, 0),
		inset 0 0 20px rgba(255, 255, 255, 0), 7px 7px 15px rgba(55, 84, 170, 0.15),
		-7px -7px 20px white, inset 0px 0px 4px rgba(255, 255, 255, 0.2);
	transition: box-shadow 299ms ease-in-out;
	display: flex;
	align-items: center;
	justify-content: center;
}

.modal__btn-close:hover {
	box-shadow: inset 7px 7px 15px rgba(47, 79, 175, 0.15),
		inset -7px -7px 20px #4f9fe6, 0px 0px 4px rgba(255, 255, 255, 0.2);
}

.modal__btn-close:active {
	border: none;
	outline: none;
}

