.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: #26282bf3;
	z-index: 50;
	border-radius: 100%;
}

.form__container {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
}

label {
	font-size: 16px;
	font-weight: 100;
}

input,
textarea,
select {
	border: none;
	outline: none;
	background: #e9eef7;
	margin: 20px;
	margin-bottom: 25px;
	font-size: 16px;
	font-weight: 100;
	transition: box-shadow 300ms ease-out;
	font-family: 'Itim', cursive;
}

input:hover,
textarea:hover,
select:hover {
	box-shadow: 10px 10px 20px #cdd1d9, -10px -10px 20px #ffffff;
}

input:focus,
textarea:focus,
select:focus {
	box-shadow: 9px 9px 16px rgba(163, 177, 198, 0.6),
		-9px -9px 16px rgba(255, 255, 255, 0.5),
		inset 3px 3px 7px rgba(136, 165, 191, 0.48), inset -3px -3px 7px #ffffff;
}

.input-title {
	height: 50px;
	padding: 25px;
	border-radius: 12px;
	box-shadow: 5px 5px 10px #cdd1d9, -5px -5px 10px #ffffff;
	width: 30rem;
}

.input-description {
	height: 200px;
	padding: 25px;
	border-radius: 12px;
	resize: none;
	box-shadow: 5px 5px 10px #cdd1d9, -5px -5px 10px #ffffff;
	width: 30rem;
}

.input-date {
	height: 50px;
	padding: 25px;
	border-radius: 12px;
	box-shadow: 5px 5px 10px #cdd1d9, -5px -5px 10px #ffffff;
	width: 20rem;
}

.input-type {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	padding: 20px 20px 20px 20px;
	border-radius: 12px;
	box-shadow: 5px 5px 10px #cdd1d9, -5px -5px 10px #ffffff;
	width: 20rem;
	cursor: pointer;
}

.tabs-container {
	display: flex;
	justify-content: center;
	align-items: center;
}

.tabs {
	display: flex;
	justify-content: center;
	align-items: center;
	list-style: none;
	border: 4px solid #ebe8e8;
	border-radius: 20px;
	background: #ececec;
	box-shadow: 9px 9px 16px rgba(163, 177, 198, 0.6),
		-9px -9px 16px rgba(255, 255, 255, 0.5),
		inset 3px 3px 7px rgba(136, 165, 191, 0.48), inset -3px -3px 7px #ffffff;
	height: 60px;
	width: 490px;
}

.tabs__item {
	/* font-family: 'Itim', cursive; */
	font-size: 17px;
	font-weight: 300;
	height: 50px;
	width: 120px;
	border-radius: 15px;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: box-shadow 0.4s ease-in;
	cursor: pointer;
}

.tabs__item:active,
.active {
	background: #ececec;
	box-shadow: 10px 10px 20px #c9c9c9;
}

.tab-move {
	z-index: 1;
	display: block;
}

